import axios from "axios";
import router from "@/routers";
import Message from "@/plugins/message-box";
import lstore from "@/plugins/lstore/lstore.js";

let cancel;
let pending = {};
const CancelToken = axios.CancelToken;
export const limit = ~~(lstore.getData("BOOTSTRAPPERS") || {}).limit || 15;
const baseURL =
  process.env.NODE_ENV === "production"
    ? `${process.env.VUE_APP_API_HOST}/api/${process.env.VUE_APP_API_VERSION}`
    : "/api/v2";
const instance = axios.create({
  baseURL,
  headers: {
    "X-Requested-With": "XMLHttpRequest"
  }
});

//
instance.interceptors.request.use(
  config => {
    //
    if (pending[config.url]) {
      pending[config.url]("");
      pending[config.url] = cancel;
    } else {
      pending[config.url] = cancel;
    }
    const TOKEN = lstore.getData("H5_ACCESS_TOKEN");
    if (TOKEN) {
      config.headers.Authorization = TOKEN;
    }
    return config;
  },
  error => {
    return Promise.reject(error);
  }
);

//
instance.interceptors.response.use(
  response => {
    return response;
  },
  err => {
    if (!axios.isCancel(err)) {
      if (err && err.response) {
        switch (err.response.status) {
          case 400:
            err.tips = "";
            break;
          case 401:
            err.tips = lstore.hasData("H5_CUR_USER") ? "" : "";
            lstore.clearData();
            router.push({
              path: "/signin",
              query: { redirect: router.currentRoute.fullPath }
            });
            break;
          case 403:
            err.tips = "";
            break;
          case 404:
            err.tips = ",";
            break;
          case 405:
            err.tips = "";
            break;
          case 408:
            err.tips = "";
            break;
          case 422: {
            const { data: { message = "" } = {} } = err.response;
            err.tips = message;
            break;
          }
          case 500:
            err.tips = "";
            break;
          case 501:
            err.tips = "";
            break;
          case 502:
            err.tips = "";
            break;
          case 503:
            err.tips = "";
            break;
          case 504:
            err.tips = "";
            break;
          case 505:
            err.tips = "http";
            break;
          default:
            err.tips = `${err.response.status}`;
        }
      } else {
        err.tips = "";
      }
      Message.error(err.tips);
    }
    return Promise.reject(err);
  }
);
export const get = (url, param) => {
  return new Promise((resolve, reject) => {
    instance({
      method: "get",
      url,
      params: param,
      cancelToken: new CancelToken(c => {
        cancel = c;
      })
    }).then(
      res => {
        resolve(res);
      },
      err => {
        reject(err);
      }
    );
  });
};
export const post = (url, param) => {
  return new Promise((resolve, reject) => {
    instance({
      method: "post",
      url,
      data: param,
      cancelToken: new CancelToken(c => {
        cancel = c;
      })
    }).then(
      res => {
        resolve(res);
      },
      err => {
        reject(err);
      }
    );
  });
};

export default instance;
